'use client'

import { useState } from 'react'
import WorkCard from '@/components/WorkCard'
import { Input } from '@/components/ui/input'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'

// Mock data untuk demo
const mockWorks = [
  {
    id: '1',
    title: 'Desain Logo Modern',
    description: 'Logo minimalis untuk startup teknologi dengan konsep yang clean dan profesional.',
    category: 'Desain',
    creator: 'Ahmad Rizki',
    price: 'Rp 150.000',
    image: '/placeholder-design.jpg'
  },
  {
    id: '2',
    title: 'Artikel SEO Marketing',
    description: 'Artikel lengkap tentang strategi digital marketing untuk UMKM dengan riset mendalam.',
    category: 'Artikel',
    creator: 'Sari Dewi',
    price: 'Rp 75.000',
    image: '/placeholder-article.jpg'
  },
  {
    id: '3',
    title: 'Aplikasi Mobile Flutter',
    description: 'Source code aplikasi mobile e-commerce dengan fitur lengkap dan dokumentasi.',
    category: 'Program',
    creator: 'Budi Santoso',
    price: 'Rp 500.000',
    image: '/placeholder-code.jpg'
  },
  {
    id: '4',
    title: 'Komposisi Musik Instrumental',
    description: 'Musik instrumental ambient untuk video, podcast, atau background musik aplikasi.',
    category: 'Musik',
    creator: 'Maya Sari',
    price: 'Rp 200.000',
    image: '/placeholder-music.jpg'
  },
  {
    id: '5',
    title: 'Template PowerPoint Bisnis',
    description: 'Set template presentasi profesional untuk proposal bisnis dan pitch deck.',
    category: 'Desain',
    creator: 'Andi Pratama',
    price: 'Rp 100.000',
    image: '/placeholder-presentation.jpg'
  },
  {
    id: '6',
    title: 'Riset Pasar E-commerce',
    description: 'Laporan riset mendalam tentang tren e-commerce Indonesia 2024 dengan data dan analisis.',
    category: 'Artikel',
    creator: 'Dina Kartika',
    price: 'Rp 125.000',
    image: '/placeholder-research.jpg'
  }
]

const categories = ['Semua', 'Desain', 'Artikel', 'Program', 'Musik']

export default function WorksPage() {
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('Semua')

  const filteredWorks = mockWorks.filter(work => {
    const matchesSearch = work.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         work.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         work.creator.toLowerCase().includes(searchTerm.toLowerCase())
    
    const matchesCategory = selectedCategory === 'Semua' || work.category === selectedCategory
    
    return matchesSearch && matchesCategory
  })

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
            Jelajahi Karya
          </h1>
          <p className="text-xl text-gray-600 max-w-2xl">
            Temukan berbagai karya kreatif dari siswa dan mahasiswa berbakat di seluruh Indonesia
          </p>
        </div>

        {/* Search and Filter */}
        <div className="bg-white rounded-lg shadow-sm border p-6 mb-8">
          <div className="flex flex-col md:flex-row gap-4 mb-6">
            <div className="flex-1">
              <Input
                type="text"
                placeholder="Cari karya, kreator, atau kata kunci..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full"
              />
            </div>
            <Button variant="outline" className="md:w-auto">
              🔍 Cari
            </Button>
          </div>

          {/* Category Filter */}
          <div className="flex flex-wrap gap-2">
            <span className="text-sm font-medium text-gray-700 mr-2 py-2">
              Kategori:
            </span>
            {categories.map((category) => (
              <Button
                key={category}
                variant={selectedCategory === category ? "default" : "outline"}
                size="sm"
                onClick={() => setSelectedCategory(category)}
                className="text-sm"
              >
                {category}
              </Button>
            ))}
          </div>
        </div>

        {/* Results Info */}
        <div className="flex justify-between items-center mb-6">
          <p className="text-gray-600">
            Menampilkan {filteredWorks.length} karya
            {selectedCategory !== 'Semua' && ` dalam kategori "${selectedCategory}"`}
            {searchTerm && ` untuk "${searchTerm}"`}
          </p>
          
          <div className="flex items-center gap-2">
            <span className="text-sm text-gray-600">Urutkan:</span>
            <Button variant="outline" size="sm">
              Terbaru
            </Button>
          </div>
        </div>

        {/* Works Grid */}
        {filteredWorks.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-12">
            {filteredWorks.map((work) => (
              <WorkCard
                key={work.id}
                id={work.id}
                title={work.title}
                description={work.description}
                category={work.category}
                creator={work.creator}
                price={work.price}
                image={work.image}
              />
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <div className="text-6xl mb-4">🔍</div>
            <h3 className="text-xl font-semibold text-gray-900 mb-2">
              Tidak ada karya ditemukan
            </h3>
            <p className="text-gray-600 mb-4">
              Coba ubah kata kunci pencarian atau filter kategori
            </p>
            <Button 
              variant="outline" 
              onClick={() => {
                setSearchTerm('')
                setSelectedCategory('Semua')
              }}
            >
              Reset Filter
            </Button>
          </div>
        )}

        {/* Load More */}
        {filteredWorks.length > 0 && (
          <div className="text-center">
            <Button variant="outline" size="lg">
              Muat Lebih Banyak
            </Button>
          </div>
        )}

        {/* CTA Section */}
        <div className="bg-white rounded-lg shadow-sm border p-8 mt-12 text-center">
          <h2 className="text-2xl font-bold text-gray-900 mb-4">
            Punya Karya yang Ingin Dimonetisasi?
          </h2>
          <p className="text-gray-600 mb-6 max-w-2xl mx-auto">
            Bergabunglah dengan komunitas kreator dan mulai menghasilkan dari karya kreatif Anda
          </p>
          <Button size="lg" asChild>
            <a href="/submit">Submit Karya Anda</a>
          </Button>
        </div>
      </div>
    </div>
  )
}
